*{
  position: relative;
}
.container-fluid{
  padding-left: .5rem;
  padding-right: .5rem;
}

.bg-e{
  background-image: url('../../images/e-2020.png');
  background-size: cover;
}
// media 侦测是否使用@2x

// 错误
.error-alert{
  background-color: rgba($color: $danger, $alpha: .2);
  border: 1px solid $danger;
  color: $danger;
  width: 100%;
  border-radius: 4px;
  text-align: center;
  padding: .75rem 0;
  margin: 1rem 0;
}
// 基礎設定
html,body{
  height: 100%;
  min-height: 100vh;
}
.mm-page{
  min-height: 100vh
}
.wrapper{
  padding-top: 48px;
  padding-bottom: 50px;
}
img.logo{
  padding-left: 11px;
}
.card.fluid{
  border-radius: 0;
  border: none;
  .card-body{
    padding: 8px 10px;
  }
}
.card.card-table{
  border-radius: 0;
  border: none;
  .card-body{
    padding: 8px 10px 0 10px;
  }
}

.white-block-login{
  background-color: #fff;
  min-height: calc(100vh - 48px - 50px);
}
.white-block-bar{
  background-color: #fff;
  min-height: calc(100vh - 48px - 50px - 26.5px);
}
// login
.tab-bar{
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 1rem;
  .tab-item{
    padding:.5rem;
    width: 26%;
    display: inline-block;
    text-align: center;
    color: $secondary;
    &.active{
      color:$primary;
      border-bottom: 2px solid $primary;
    }
  }
}

// Gamebox
.game-box{
  padding: 1.5rem 0;
}
.game-title{
  color: $primary;
  font-weight: $font-weight-bold;
  text-align: center;
  margin-bottom: .75rem;
  margin-top: 2rem;
}
.game-list{
  background-color: #fff;
  padding: .5rem .5rem 0 .5rem;
  margin-bottom: .5rem;
  .game-block{
    font-weight: $font-weight-light;
  }
}
dl.option-title{
  // margin-bottom: .25rem;
  dd{
    color:$secondary;
    &:hover{
      color:$secondary;
    } 
  }
}
dd.trading-volume{
  text-align: right;
  color:$black;
}
dd.odds,.ratio{
  font-size: 1.25rem;
  font-weight: $font-weight-bold;
}
dd.ratio{
  color:$black;
}

.game-team{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: 0;
  height: 42px;
  .col-5{
    flex:  0 0 46%;
    width: 100%;
    max-width: 46%;
    padding: 0 .5rem;
  }
  .col-center{
    flex:  0 0 8%;
    width: 100%;
    max-width: 8%;
    font-weight: $font-weight-bold;
    color:$primary;
    padding: 0;
    font-size: 16px;
  }
}
.team-flow{
  color:$black;
  overflow:hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

// about
.about-logo{
  width: 9rem;
}

// bar
.bar{
  background-color: $primary;
  text-align: center;
  color: $white;
  padding-top: 0;
  padding-bottom: .75rem;
  font-weight: $font-weight-bold;
  letter-spacing: .8px;
}
.title-item{
  display: flex;
  align-items: center;
}

// game-list-form
.form-date{
  color: $primary;
  font-size: 1.1rem;
  font-weight: 500;
}
.main-list{
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: .5rem;
  .col-title{
    position: relative;
    width: 100%;
    flex: 0 0 30%;
    max-width: 80px;
  }
  .col-text{
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
  }
}
.border-bottom-su{
  border-bottom: 1px dashed $gray-400;
  padding-bottom: .5rem;
}

// 提示框
.prompt-box{
  border: 1px solid $borderColor;
  border-radius: 6px;
  color: $gray-500;
  padding: .25rem;
  font-size: 80%;
}

// card
.card-header{
  background-color: #fff;
  color: $primary;
  font-weight: $font-weight-bold;
}
.card-date{
  padding: .25rem 0;
  text-align: center;
}

// 结算报表
.info-block{
  background-color: $primary;
  border: 2px solid $primary;
  padding: .5rem;
  border-radius: .5rem;
  margin-bottom:.5rem;
  .title{
    color:rgba($color: $white, $alpha: .7);
  }
  .info{
    color: $white;
    font-size: 1.25rem;
  }
}
.info-outline-block{
  border: 2px solid $primary;
  padding: .5rem;
  border-radius: .5rem;
  margin-bottom:.5rem;
  .title{
    color:$secondary
  }
  .info{
    color: $primary;
    font-size: 1.25rem;
  }
}
.result-card{
  width: 100%;
}
.result-card{
  position: absolute;
  transform: translate(-50%,-50%);
  top: 38%;
  left: 50%;
  max-width: 400px;
}
@media (min-width: 576px) { 
  .login-card{
    position: absolute;
    transform: translateX(-50%);
    top: 6%;
    left: 50%;
    width: 100%;
    max-width: 480px;
  }
}
// 首页
.linebox{
  &::before{
    content:"";
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    right: 0;
    height: 2.2rem;
    background-color: $primary;
  }
  &::after{
    content: '';
    position: absolute;
    top: calc(2.2rem - .6rem);
    left: .8rem;
    right: .8rem;
    height: .6rem;
    z-index: 1;
    background: linear-gradient(270deg,#28dfff,#0890ff);
  }
}
.index-footer{
  position: relative;
  background-color: #fff;
  // padding-top: 4rem;
  // &::after {
  //   content: '';
  //   position: absolute;
  //   top: 2.5rem;
  //   left: .8rem;
  //   right: .8rem;
  //   height: .6rem;
  //   z-index: 1;
  //   background: linear-gradient(270deg,#28dfff,#0890ff);
  // }
  li{
    // border-top: 1px solid $gray-400;
    height: 80px;
    max-height: 80px;
    img{
      display: inline-block;
      padding:1rem .5rem;
      max-height: 100%;
      position: absolute;
      transform: translate(-50%,-50%);
      top: 50%;
      left: 50%;
    }
  }
  // li:child(1),:child(2){
  //   border-top: 0;
  // }
  // li:nth-child(2n){
  //   border-left: 1px solid $gray-400;
  // }
  .usdt{
    width: 50px;
  }
}
a.more{
  padding: .5rem 1rem;
  width: 100%;
  display: block;
  text-align: right;
  color: $black;
  font-weight: $font-weight-bold;
}
.team-list{
  display: flex;
  align-items: center;
  padding-bottom: .75rem;
  padding-top: .75rem;
  border-bottom: 1px solid $gray-200;
  span{
    font-size: 14px;
    font-weight: $font-weight-bold;
  }
}
img.icon-team{
  width: 25px;
  margin-right: 1rem;
}

// 结果页面
.envelop{
  display: block;
  margin: 0 auto;
  width: 100px;
}
.result-title{
  color: $primary;
  font-weight: $font-weight-bold;
  text-align: center;
  margin-top: .75rem;
}
.result-info{
  text-align: center;
  color:$secondary;
}
// 设定密码页面
.setting-pw{
  padding: 2rem 1rem;
}

.modal-header.border-none{
  border-bottom:none;
  padding-bottom: 0;
}
.modal-title{
  color: $primary;
  font-weight: $font-weight-bold;
}

// 点数中心
.address-block{
  text-align: center;
  color: $secondary;
  margin-bottom: 1rem;
  #address{
    color:$black;
  }
}
.total{
  padding: .75rem 0;
  border-top: 1px solid $lightGray;
  border-bottom: 1px solid $lightGray;
  margin-bottom: 1rem;
}

// user 用户
.user-account{
  background-color: $white;
  .account-body{
    padding: 1rem;
    border-bottom: 1px solid $borderColor;
    &:nth-child(2){
      border-bottom: 0;
    }
    .qr-link{
      padding: .75rem 0 .75rem .75rem;
      color: $secondary;
    }
    .account-title{
      font-size: .875rem;
      // color: $secondary;
      margin-bottom: .25rem;
    }
    .account-value{
      color: $primary;
      font-weight: 600;
      // margin-bottom: 0;
    }
    .account-block{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .account-id{
      color: $secondary;
      font-weight: $font-weight-light;
    }
  }
}
.cody-url{
  display: inline-block;
  padding: .4375rem 1.25rem;
  color: $black;
  border: 1px solid $borderColor;
  border-radius: 4px;
  margin-top: .5rem;
  margin-bottom: 1rem;
}
